.forms {
    padding: 60px 0 120px;
    h3 {
        margin-bottom: 40px;
    }
    form {
        .input-group {
            margin-bottom: 30px;
            input {
                display: block;
                width: 100%;
                border: 1px solid #e7e7e7;
                font-size: 15px;
                line-height: 100%;
                font-weight: 300;
                padding: 9px 8px;
                @include placeholderColor(#989898);
                &:focus {
                    @include hidePlaceholder();
                }
                + label {
                    color: $red;
                    margin-top: 2px;
                    text-transform: uppercase;
                    font-weight: bold;
                }
            }
            input {
                &[type="checkbox"] {
                    display: none;
                }
                &[type="checkbox"] + label {
                    padding-left: 42px;
                    position: relative;
                    color: #8e8c8c;
                    cursor: pointer;
                    margin-top: 0;
                    text-transform: none;
                    font-weight: 400;
                    &::before,
                    &::after {
                        position: absolute;
                        content: "";
                        display: block;
                    }
                    &::before {
                        left: 0;
                        top: 4px;
                        width: 25px;
                        height: 25px;
                        border: 1px solid #e7e7e7;
                    }
                    &::after {
                        left: 8px;
                        top: 5px;
                        width: 10px;
                        height: 15px;
                        transform: rotate(45deg);
                        border-bottom: 1px solid #8e8c8c;
                        border-right: 1px solid #8e8c8c;
                        transition: all 0.3s ease;
                        opacity: 0;
                    }
                }
                &[type="checkbox"]:checked + label {
                    &::after {
                        opacity: 1;
                    }
                }
            }
        }
        a {
            font-family: $roboto;
            color: $aqua;
            &:hover {
                opacity: 0.7;
            }
        }
        .form-buttons {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            input {
                margin-right: 10px;
                margin-bottom: 10px;
                flex-shrink: 0;
            }
            p {
                font-size: 15px;
                line-height: 22px;
                color: #8e8c8c;
                max-width: 240px;
                text-align: right;
            }
        }
    }

    .sign-in-form {
        @include media-breakpoint-down(sm) {
            margin-bottom: 20px;
        }
    }
    .register-form {
    }
}
