/* buttons */

.btn {
    white-space: normal;
    display: inline-block;
    border: 1px solid $gray2;
    color: $gray2;
    padding: 16px 28px;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 3px;
    border-radius: 0;
    @include media("<mddesktop") {
        padding: 14px 20px;
    }
    @include media-breakpoint-down(sm) {
        letter-spacing: 1.5px;
        padding: 8px 14px;
        font-size: 14px;
    }
    &:hover {
        background-color: $aqua;
        border-color: transparent;
        color: $white;
    }
}
