.section-lookbook {
    padding-bottom: 120px;
    @include media-breakpoint-down(sm) {
        padding-bottom: 20px;
    }
    .row {
        > [class*="col-"] {
            @include media("<mddesktop") {
                margin-bottom: 30px;
            }
        }
    }
}
.lookbook {
    min-height: 535px;
    position: relative;
    @include media-breakpoint-down(sm) {
        min-height: 480px;
    }
    img {
        max-width: 100%;
    }
    .lookbook-inner {
        right: 0;
        top: 0;
        position: absolute;
        width: 370px;
        max-width: 100%;
        height: 100%;
        padding-right: 55px;
        padding-top: 20px;
        padding-bottom: 20px;
        padding-left: 20px;
        text-align: right;
        @include media-breakpoint-down(xs) {
            padding-top: 10px;
            padding-bottom: 10px;
            padding-right: 10px;
        }
    }
    h3 {
        margin-top: 70px;
        text-transform: uppercase;
        color: $brown;
        font-size: 48px;
        line-height: 54px;
        margin-bottom: 18px;
        span {
            display: block;
            &:last-child {
                font-weight: 300;
            }
        }
    }
    p {
        font-size: 15px;
        font-family: $roboto;
        line-height: 22px;
        margin-bottom: 24px;
        color: $gray;
    }
    .lookbook-btn {
    }
}
