// main-screen
.main-screen {
    position: relative;
    background-size: cover;
    > .container-large {
        position: relative;
        z-index: 6;
    }
    &.not-full {
        .main-screen-inner {
            min-height: 450px;
            text-align: center;
            color: $white;
        }
        h1 {
            font-size: 48px;
            line-height: 54px;
            text-transform: uppercase;
            margin-bottom: 20px;
            @include media-breakpoint-down(sm) {
                font-size: 42px;
                line-height: 50px;
            }
            span {
                font-weight: 300;
            }
        }
        .main-screen-subtitle {
            text-transform: uppercase;
            font-size: 30px;
            line-height: 54px;
            color: $white;
            @include media-breakpoint-down(sm) {
                font-size: 26px;
                line-height: 40px;
            }
        }
    }
    .main-screen-inner {
        padding-top: 108px;
        padding-bottom: 108px;
        min-height: calc(100vh - 44px);
        display: flex;
        justify-content: center;
        flex-direction: column;
        @include media-breakpoint-down(sm) {
            min-height: 400px;
        }
    }
    .main-screen-title {
        font-weight: bold;
        display: flex;
        justify-content: space-between;
        font-size: 450px;
        color: $brown;
        padding: 0 115px;
        margin-top: 50px;
        @include media("<1440px") {
            padding: 0 !important;
        }
        @include media-breakpoint-down(lg) {
            font-size: 250px;
        }
        @include media-breakpoint-down(sm) {
            font-size: 150px;
        }
        @include media-breakpoint-down(xs) {
            font-size: 115px;
        }
    }
    .main-screen-btn {
        text-align: right;
        margin-top: 30px;
        padding-right: 190px;
        @include media-breakpoint-down(lg) {
            padding-right: 70px;
        }
        @include media-breakpoint-down(sm) {
            padding-right: 0;
        }
    }
}

.bg-mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    background-color: $black;
    opacity: 0.5;
}

.breadcrumbs {
    display: flex;
    justify-content: center;
    font-size: 30px;
    line-height: 54px;
    text-transform: uppercase;
    @include media("<mddesktop") {
        font-size: 20px;
        line-height: 30px;
    }
    @include media-breakpoint-down(md) {
        font-size: 16px;
        line-height: 24px;
    }
    @include media-breakpoint-down(sm) {
        flex-direction: column;
    }
    li {
        padding: 0 15px;
        position: relative;
        &:not(:last-child) {
            &::after {
                content: "";
                position: absolute;
                top: 50%;
                right: -4px;
                width: 8px;
                height: 2px;
                background-color: $white;
                @include media-breakpoint-down(sm) {
                    display: none;
                }
            }
        }
        &:last-child {
            color: $aqua;
        }
    }
}
