.card-input {
    display: flex;
    position: relative;
    width: 65px;
    .plus,
    .minus {
        position: absolute;
        width: 30px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #e7e7e7;
        height: 50%;
        width: 33px;
        right: 0;
        cursor: pointer;

        &:hover {
            opacity: 0.8;
        }

        i{
            color: #989898;
        }
    }

    .plus {
        top: 0;
    }
    .minus {
        bottom: 0;
    }

    .display {
        width: 33px;
        height: 35px;
        border: 1px solid #e7e7e7;
        padding: 5px;
        font-size: 13px;
        line-height: 100%;
        color: #989898;
        font-weight: 300;
        font-weight: bold;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}
