// vars
$images: '../images/';
$fonts: '../fonts/';
$fa-font-path: '../fonts';
// fonts
$proxima: 'Proxima Nova';
$montserrat: 'Montserrat';
$roboto: 'Roboto Bk';
$iconfont: 'FontAwesome';
$breakpoints: ( phone: 375px, tablet: 768px, desktop: 992px, minscroll: 1025px, mddesktop: 1200px);
// colors
$white: #fff;
$white2: #f8f8f8;
$black: #000;
$black2: #333333;
$black3: #222222;
$gray: #727272;
$gray2: #454647;
$gray3: #a9a9a9;
$gray4: #999;
$blue: #248bdc;
$brown: #575153;
$aqua: #00c8c8;
// sizes
$grid-gutter-width: 30px;
$grid-gutter-width-mobile: 40px;
$container-large-desktop: (1780px + $grid-gutter-width);
$container-width: (1170px + $grid-gutter-width);
$container-fluid-width: (1460px + $grid-gutter-width);
$text-color: $black;
$font-family-base: $montserrat;
$font-size-base: 1rem;
$line-height-main: 24px;
$headings-font-weight: 700;
$link-color: $gray;
$link-hover-color: $aqua;
// bootstrap buttons
$btn-border-radius-base: 0;
$btn-border-radius-small: 0;
$btn-border-radius-large: 0;
$btn-default-bg: rgba(lighten($black, 88.63), .97);
$btn-default-color: $black;
$btn-default-border: transparent;
$btn-info-bg: lighten($black, 94.51);
$btn-info-color: $blue;
$btn-info-border: transparent;
$btn-primary-bg: $blue;
$btn-primary-color: $white;
$btn-primary-border: $blue;