.clothing {
    padding-bottom: 60px;
    @include media-breakpoint-down(sm) {
        padding-bottom: 40px;
    }
    .container {
    }
    .row {
    }
    .col-md-6 {
    }
    .col-md-12 {
    }
    .clothing-box {
        margin-top: 60px;
        @include media-breakpoint-down(sm) {
            margin-top: 40px;
        }
    }
    .margin {
    }
}
.clothing-item {
    &.margin {
        h3 {
            margin-top: 110px;
            @include media-breakpoint-down(sm) {
                margin-top: 40px;
            }
        }
    }
    .item-picture {
        padding-bottom: 70%;
    }
    h3 {
    
        margin-bottom: 25px;
        margin-top: 28px;
        @include media-breakpoint-down(sm) {
            margin-top: 18px;
            margin-bottom: 14px;
        }
    }
    h5 {
        
        margin-bottom: 15px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 10px;
        }
    }
    p {
        strong {
            color: $aqua;
            font-weight: 400;
        }
        a {
            color: $aqua;
            &:hover {
                opacity: 0.7;
            }
        }
    }
}
.brands {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    .brands-item {
        flex: 0 1 33.333%;
        padding: 0 15px;
        text-align: center;
        @include media-breakpoint-down(xs) {
            flex: 0 1 100%;
            margin-bottom: 30px;
        }
        img {
            max-width: 100%;
            @include media-breakpoint-down(xs) {
                width: 60%;
            }
        }
    }
}
