.shop-cards {
    padding-top: 65px;
    padding-bottom: 65px;
    .shop-cards-menu {
        border-top: 1px solid #eeeeee;
        padding: 50px 0;
        display: flex;
        margin-left: -15px;
        margin-right: -15px;
        li {
            padding: 0 15px;
            a {
                font-size: 13px;
                line-height: 20px;
                text-transform: uppercase;
            }
        }
    }
    .row {
        &.row-second {
            > [class*="col-"] {
                &:nth-last-of-type(2) {
                    @include media-breakpoint-down(md) {
                        order: 99;
                    }
                }
            }
        }
        > [class*="col-"] {
            margin-bottom: 30px;
        }
    }
}

.shop-card {
    background-size: cover;
    position: relative;
    padding-bottom: 118%;
    background-color: $white2;
    transition: all 0.3s ease;

    &.large {
        padding-bottom: 80%;
        @include media-breakpoint-down(sm) {
            padding-bottom: 120%;
        }
    }

    &.large-second {
        padding-bottom: 0;
    }

    &:hover {
        box-shadow: 0 0 20px $gray3;
        .card-footer {
            visibility: visible;
            opacity: 1;
            box-shadow: 0 5px 15px $gray3, -5px 5px 15px $gray3, 5px 5px 15px $gray3;
        }
        .miniatures {
            visibility: visible;
            opacity: 1;
        }
        .card-buttons {
            visibility: visible;
            opacity: 1;
        }
    }
    .mask {
        display: none;
        background-color: $white;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0.6;
        z-index: 2;
        @include media("<mddesktop") {
            display: block;
        }
    }
    .card-content {
        position: relative;
        z-index: 3;
        padding: 15px;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        &.flex {
            p {
                max-width: 500px;
            }
            .card-content-box {
                &:last-child {
                    position: absolute;
                    right: 15px;
                    top: 50%;
                    transform: translateY(-40%);
                    text-align: right;
                    @include media("<mddesktop") {
                        display: none;
                    }
                    p {
                        text-transform: uppercase;
                        color: $aqua;
                        &:first-child {
                            font-size: 200px;
                            line-height: 170px;
                            margin-right: -10px;
                        }
                        &:last-child {
                            font-size: 72px;
                            line-height: 54px;
                        }
                    }
                }
            }
        }
        h2 {
            text-transform: uppercase;
            margin-top: 25px;
            margin-bottom: 20px;
            @include media("<mddesktop") {
                margin-top: 0;
            }
            span {
                font-weight: 300;
            }
        }
        h5 {
            margin-bottom: 15px;
        }
        p {
            max-width: 300px;
            margin-bottom: 20px;
        }
    }
    
    .miniatures {
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease;
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 10px;
        right: 10px;
        img {
            display: block;
            width: 90px;
            border: 1px solid $white;
            cursor: pointer;

            &:hover {
                box-shadow: 0 0 6px $gray3;
            }

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
    .card-buttons {
        position: absolute;
        width: 100%;
        display: flex;
        justify-content: center;
        top: 60%;
        left: 50%;
        transform: translate(-50%, 0);
        font-size: 50px;
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease;
        a {
            display: inline-block;
            margin: 0 12px;
            color: $white;
            &:hover {
                color: $aqua;
            }
        }
    }
    .card-footer {
        visibility: hidden;
        opacity: 0;
        transition: all 0.3s ease;
        position: absolute;
        background-color: $white;
        text-align: center;
        padding: 10px 15px;
        bottom: 0;
        width: 100%;
        transform: translateY(100%);
        z-index: 10;
        h6 {
            font-size: 13px;
            color: $gray;
            text-transform: uppercase;
            margin-bottom: 20px;
        }

        p {
            font-size: 15px;
            font-family: $roboto;
            color: #8e8c8c;
            margin-bottom: 10px;
        }
        .card-buttons {
            display: flex;
            justify-content: center;
            transform: translate(0);
            position: static;
            li {
                margin: 0 8px;
                a {
                    display: flex;
                    border-radius: 50%;
                    justify-content: center;
                    align-items: center;
                    background-color: $gray;
                    width: 30px;
                    height: 30px;
                    
                    &:hover {
                        background-color: $aqua;
                        i {
                            // color: $white
                            font-size: 20px;
                        }
                    }

                    i {
                        color: $white;
                        font-size: 16px;
                    }
                }
            }
        }
    }
}
.price {
    position: absolute;
    top: 10px;
    left: 10px;
    color: #9a9a9a;
    font-weight: 300;
    font-size: 21px;
    line-height: 100%;
    text-transform: uppercase;
    span {
        position: relative;
        sup {
            font-size: 13px;
        }
        &:not(:first-child) {
            margin-left: 5px;
        }
        &.with-separator {
            &::before {
                content: "";
                position: absolute;
                display: block;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                width: calc(100% - 10px);
                height: 1px;
                background-color: $gray;
            }
        }
    }
}