@mixin position-side() {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

@mixin placeholderColor($color) {
    &::-webkit-input-placeholder {
        color: $color;
        opacity: 1;
    }
    &:-moz-placeholder {
        /* Firefox 18- */
        color: $color;
        opacity: 1;
    }
    &::-moz-placeholder {
        /* Firefox 19+ */
        color: $color;
        opacity: 1;
    }
    &:-ms-input-placeholder {
        color: $color;
        opacity: 1;
    }
}

@mixin hidePlaceholder {
    &:focus::-webkit-input-placeholder {
        color: transparent;
        opacity: 0;
    }
    &:focus::-moz-placeholder {
        color: transparent;
        opacity: 0;
    }
    &:focus:-moz-placeholder {
        color: transparent;
        opacity: 0;
    }
    &:focus:-ms-input-placeholder {
        color: transparent;
        opacity: 0;
    }
}

@mixin zeroBorderRadius() {
    -moz-appearance: none;
    -webkit-appearance: none;
    -webkit-border-radius: 0px;
    border-radius: 0;
}

@mixin vCenter {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}

@mixin hCenter {
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%)
}


@mixin hideSelectArrow{
    &::-ms-expand {
        display: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
    }
}