.select-wrapper {
    position: relative;
    height: 35px;
    @include media-breakpoint-down(xs) {
        margin-bottom: 20px;
    }
}

.select {
    display: none;
}

.select2 {
    width: 100% !important;
}
.select2-search__field {
    display: none;
}

.select2-selection {
    &:focus {
        outline: none;
    }
}

.select2-dropdown {
    display: block;
}

.select2-selection__rendered {
    overflow: hidden;
    display: block;
    font-size: 13px;
    font-weight: 300;
    line-height: 100%;
    background: #fff;
    padding: 10px 40px 10px 20px;
    border: 1px solid #e7e7e7;
    cursor: pointer;
    position: relative;
    min-width: 170px;
    color: #41130a;
    text-overflow: ellipsis;

    &:focus {
        outline: none;
    }
    &::before {
        content: "";
        position: absolute;
        width: 7px;
        height: 7px;
        border: 0px solid #989898;
        border-right-width: 2px;
        border-bottom-width: 2px;
        right: 20px;
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
        transition: transform 0.25s;
        transform-origin: 50% center;
    }
}

.select2-search__field {
    display: none;
}

.select2-container {
    display: block;
}
.select2-container--open {
    .select2-selection__rendered {
        &::before {
            transform: translateY(-50%) rotate(-135deg);
        }
    }
}
.select2-results__options {
    margin-top: 7px;
    background-color: $white;
    list-style: none;
    width: 100%;
    border: none;
    padding: 15px 0;
    box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.0524749);
    border-radius: 4px;
    @include media-breakpoint-down(xs){
    position: relative;
    z-index: 10;
    }
    li {
        padding: 10px 35px 10px 30px;
        cursor: pointer;
        color: $dark;
        &[id*="-0"] {
            color: $gray;
        }
        &:hover {
            background: rgb(231, 231, 231);
        }
    }
}
