.cities {
    padding-top: 60px;
    padding-bottom: 125px;
    @include media-breakpoint-down(sm) {
        padding-bottom: 0;
    }
    .tabs-item {
        margin-bottom: 60px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 30px;
        }
    }
    .tab {
        h3 {
            margin-bottom: 20px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 15px;
            }
        }
        h5 {
            margin-bottom: 15px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 10px;
            }
        }
        p {
            margin-bottom: 20px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 15px;
            }
        }
        @include media-breakpoint-down(sm) {
            margin-bottom: 35px;
        }
    }
    .active {
        h3 {
            text-shadow: 0 0 1px $aqua;
        }
    }
    .tabs-content-wrap {
        [class*="col-"] {
            background-color: $white2;
            position: relative;
        }
    }
    .tab-content-item {
        &:not(:first-child) {
            display: none;
        }
    }

    .map-box {
        position: absolute;
        width: calc(100% - 15px);
        height: 100%;
        min-height: 530px;
        @include media-breakpoint-down(md) {
            height: 340px;
            width: 100%;
            min-height: auto;
            position: relative;
        }
    }
    .description {
        padding: 40px 10px;

        @include media-breakpoint-down(sm) {
            padding: 20px 10px;
        }
        h3 {
            margin-bottom: 25px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 15px;
            }
        }
        h5 {
            margin-bottom: 15px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 8px;
            }
        }
        p {
            margin-bottom: 25px;
            @include media-breakpoint-down(sm) {
                margin-bottom: 15px;
            }
        }
    }
    .contacts {
        margin-bottom: 70px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 25px;
        }
        address {
            margin: 0;
        }
        li {
            position: relative;
            padding-left: 35px;
            font-weight: 300;
            color: #8e8c8c;
            margin-bottom: 8px;
        }
        i {
            position: absolute;
            left: 0;
            top: 3px;
            display: inline-block;
            margin-right: 10px;
        }
    }
    .socials {
        display: flex;
        margin-left: -15px;
        margin-right: -15px;
        li {
            margin: 0 15px;
            a {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background-color: $gray;
                color: $white;
                &:hover {
                    color: $aqua;
                }
            }
            i {
                font-size: 25px;
            }
        }
    }
}
