/* footer */

.footer {
    background-color: $white2;
    padding-top: 60px;
    @include media-breakpoint-down(sm) {
        padding-top: 20px;
    }
    p {
      font-family: $montserrat;
    }
    .footer-row {
        display: flex;
        flex-wrap: wrap;
        margin-left: -20px;
        margin-right: -20px;
        margin-bottom: 40px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 20px;
        }
    }
    .footer-row + .row {
        margin-bottom: 53px;
        @include media-breakpoint-down(sm) {
            margin-bottom: 20px;
        }
    }
    .footer-box {
        flex: 0 1 20%;
        padding-left: 20px;
        padding-right: 20px;
        @include media("<mddesktop") {
            flex: 0 1 33.333%;
            margin-bottom: 15px;
        }
        @include media-breakpoint-down(sm) {
            flex: 0 1 50%;
        }
        @include media-breakpoint-down(xs) {
            flex: 0 1 100%;
            margin-bottom: 25px;
        }
        h5 {
            font-weight: bold;
            font-size: 13px;
            line-height: 20px;
            margin-bottom: 20px;
            text-transform: uppercase;
            @include media-breakpoint-down(xs) {
                margin-bottom: 10px;
            }
        }
        p,
        a {
            font-size: 14px;
            line-height: 30px;
            font-weight: 300;
        }
    }
    .socials {
        background-color: $aqua;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        color: $white;
        width: 100%;
        min-height: 200px;
        li {
            margin: 0 25px;
            @include media-breakpoint-down(sm) {
                margin: 0 15px;
            }
            a {
                color: $white;
                font-size: 40px;
                @include media-breakpoint-down(sm) {
                    font-size: 36px;
                }
                &:hover {
                    color: $brown;
                }
            }
        }
    }
    > .row {
        background-color: $black2;
        color: #999999;
        padding: 13px 15px;
        p {
            text-align: right;
            @include media-breakpoint-down(sm) {
                text-align: left;
            }
        }
        span {
            display: inline-block;
            &:first-child {
                margin-right: 30px;
            }
        }
    }
}
