html {
    &.menu-opened {
        touch-action: none;
        overscroll-behavior: none;
        -webkit-overflow-scrolling: auto;
        overflow: hidden;
    }
}

body {
    min-width: 320px;
    font-family: $font-family-base;
    font-size: $font-size-base;
    line-height: $line-height-main;
    color: $text-color;
    font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    @include media("<desktop") {
        font-size: 16px;
        line-height: 1.5em;
    }
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: none;
    cursor: pointer;
    transition: all 0.3s;
}

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
    border: none;
    padding: 0;
}

blockquote,
q {
    quotes: none;
}

q:before,
q:after {
    content: "";
}

a {
    transition: all 0.3s;
}

a,
button,
input,
textarea,
select {
    &:focus {
        outline: none;
    }
}

.wrapper {
    position: relative;
    overflow: hidden;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-top: 44px;
    .content {
        flex-grow: 1;
        .main-screen {
            margin-top: 108px;
            @include media-breakpoint-down(md) {
                margin-top: 54px;
            }
        }
    }
    &.white-header {
        .header {
            background-color: $white;
        }
    }
}

.container,
.container-fluid {
    @include media("<desktop") {
        padding-left: $grid-gutter-width-mobile / 2;
        padding-right: $grid-gutter-width-mobile / 2;
    }
}
.container-large {
    margin: 0 auto;
    max-width: $container-large-desktop;
    padding-right: $grid-gutter-width / 2;
    padding-left: $grid-gutter-width / 2;
}
.container {
    margin: 0 auto;
    max-width: $container-width;
    padding-right: $grid-gutter-width / 2;
    padding-left: $grid-gutter-width / 2;
}

.bg-img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

h1,
.h1 {
    margin: 0;
}

h2,
.h2 {
    margin: 0;
    font-size: 48px;
    line-height: 54px;
    color: $brown;
}

h3,
.h3 {
    margin: 0;
    font-size: 18px;
    line-height: 20px;
    text-transform: uppercase;
    color: $brown;
}

h4,
.h4 {
    margin: 0;
}

h5,
.h5 {
    margin: 0;
    font-size: 13px;
    line-height: 20px;
    text-transform: uppercase;
    color: $gray;
}

h6,
.h6 {
    margin: 0;
}

p {
    margin: 0;
    font-size: 15px;
    line-height: 22px;
    color: #8e8c8c;
    font-family: $roboto;
}

.slick-slide {
    &:focus {
        outline: none !important;
    }
}

.row {
    &-flex {
        @include media(">=tablet") {
            display: flex;
            flex-wrap: wrap;
            > [class*="col-"] {
                flex: 0 1 auto;
            }
        }
    }
}

.div[class|="col-"] {
    position: relative;
}

a {
    &:hover {
        text-decoration: none;
    }
}
img.lazyload {
    opacity: 0;
    transition: opacity 0.3s;

    &.lazyloaded {
        opacity: 1;
    }
}

.lazyload-img-to-bg {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
}
.black-mask {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: $black;
    opacity: 0;
    visibility: hidden;
    z-index: 50;
    position: fixed;
    transition: all 0.5s ease-in-out;
    &.menu-opened {
        opacity: 0.6;
        visibility: visible;
    }
}

.list-unstyled {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(225deg, gray 10%, purple, gray);
    width: 100%;
    min-height: 100vh;
    overflow: auto;
    padding: 50px 0;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 20px;
    li{
        padding: 15px 0;
    }
    a {
        &[href*='homepage']{
            font-size: 25px;
            text-shadow: 2px 3px 0 black, 2px 4px 0 blue;
        }
        color: $white;
        &:hover {
            color: red;
        }
    }
}
