/* header */

.header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 70;
    transition: all 0.3s ease;
    @include media-breakpoint-down(md) {
        padding-bottom: 8px;
    }
    .header-row {
        background-color: $black2;
        font-size: 13px;
        line-height: 21px;
    }
    .banner {
        margin-top: 50px;
        @include media-breakpoint-down(md) {
            display: none;
        }
    }
    .language {
        height: 100%;
        display: flex;
        align-items: center;
        width: 120px;
        .arrow {
        }
        ul {
            display: none;
        }
    }

    .sr-only {
    }
    .col-md-8 {
    }
    .header-box {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        @include media-breakpoint-down(xs) {
            flex-direction: column;
            align-items: flex-end;

            > div {
                padding-top: 5px;
                padding-bottom: 5px;
                padding-right: 5px;
            }
        }
        a {
            &:first-child {
                margin-right: 30px;
            }
        }
    }
    .basket {
        margin-left: 80px;
        background: $aqua;
        width: 147px;
        padding: 11px 10px;
        display: inline-block;
        text-align: center;
        color: $white;
        @include media-breakpoint-down(xs) {
            margin-left: 20px;
            padding: 3px 5px;
            width: 140px;
        }
        > span {
            &:not(.arrow) {
                margin: 0 10px;
            }
        }
        .arrow {
            &::before,
            &::after {
                background-color: $white;
            }
        }
        i {
            font-size: 20px;
        }
    }

    .container-large {
        // padding: 32px 0;
    }
    .logo {
        display: block;
        margin-top: 35px;
        font-size: 24px;
        line-height: 100%;
        font-weight: bold;
        text-transform: uppercase;
        @include media-breakpoint-down(md) {
            margin-top: 5px;
        }
        @include media-breakpoint-down(xs) {
            font-size: 16px;
        }
        span {
            font-weight: 400;
        }
    }
    .col-md-9 {
    }
    .header-large-box {
        display: flex;
        align-items: center;
    }
    .header-menu {
        margin-left: auto;
        display: flex;
        margin-top: 11px;
        margin-right: 81px;
        transition: all 0.3s ease;
        position: relative;
        > .open-menu {
            position: absolute;
            top: 5px;
            right: 15px;
            .bar2 {
                opacity: 0;
            }
            .bar1 {
                transform: rotate(-45deg) translate(0px, 8px);
            }
            .bar3 {
                transform: rotate(45deg) translate(-4px, -13px);
            }
        }
        @include media("<1440px") {
            margin-right: 0;
        }

        > li {
            margin: 0 27px;
            position: relative;

            @include media("<1440px") {
                margin: 0 12px;
            }

            &.hover {
                > ul {
                    visibility: visible;
                    opacity: 1;
                }
            }
            @include media-breakpoint-up(lg) {
                &:hover {
                    > a {
                        color: $aqua;
                    }
                    > ul {
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
            > .arrow {
                position: absolute;
                left: 0;
                // left: 27px;
                margin-left: 0;
                top: 56px;
            }

            > a {
                font-weight: bold;
                display: block;
                font-size: 13px;
                line-height: 20px;
                padding: 32px 0 45px;
                // padding: 32px 27px 45px;
                text-transform: uppercase;
            }
            > ul {
                top: 96px;
                position: absolute;
                box-shadow: 4px 4px 4px $gray3, -4px 4px 4px $gray3;
                padding: 12px 27px 27px;
                width: 460px;
                transition: all 0.3s ease;
                visibility: hidden;
                opacity: 0;
                background-color: $white;
                @include media-breakpoint-down(sm) {
                    max-width: 100%;
                }
                > li {
                    &:first-child {
                        display: flex;
                        > div {
                            &:last-child {
                                padding-left: 27px;
                            }
                        }
                    }
                    > div {
                        flex: 0 1 50%;

                        > a {
                            display: block;
                            font-size: 12px;
                            line-height: 20px;
                            text-transform: uppercase;
                            font-weight: bold;
                            margin-bottom: 19px;
                        }
                        ul {
                            > li {
                                font-size: 14px;
                                line-height: 30px;
                            }
                        }
                    }
                }
            }
        }

        @include media-breakpoint-down(md) {
            position: fixed;
            width: 320px;
            height: 100%;
            top: 0;
            left: 0;
            background-color: $white;
            z-index: 55;
            margin-top: 0;
            flex-direction: column;
            padding: 40px 0;
            overflow: auto;
            transform: translateX(-100%);

            &.menu-opened {
                transform: translateX(0);
            }

            > li {
                text-align: center;
                border-bottom: 1px solid $gray3;
                &.opened {
                    > ul {
                        height: auto;
                        visibility: visible;
                        opacity: 1;
                        margin-bottom: 20px;
                        padding-bottom: 10px;
                    }
                }
                > a {
                    padding: 24px 0;
                    font-size: 24px;
                }
                > .arrow {
                    right: 0;
                    left: auto;
                    top: 26px;
                }
                > ul {
                    position: relative;
                    top: 20px;
                    width: 100%;
                    height: 0;
                    padding: 0;
                    box-shadow: none;

                    transition: height 0.3s ease;
                    > li {
                        > div {
                            &:not(:last-child) {
                                margin-bottom: 25px;
                            }
                            > a {
                                margin-bottom: 10px;
                                font-size: 17px;
                            }
                        }
                        &:first-child {
                            flex-direction: column;
                            > div {
                                padding-left: 0 !important;
                            }
                        }
                    }
                }
            }
        }
    }
    .search {
        margin-top: 6px;
        margin-right: 8px;
        position: relative;
        @include media-breakpoint-down(xl) {
            margin-right: 0;
        }
        @include media-breakpoint-down(md) {
            margin-left: auto;
        }
        input {
            width: 191px;
            border-radius: 0;
            border: 1px solid #e7e7e7;
            font-weight: 300;
            font-size: 13px;
            line-height: 100%;
            padding: 6px 38px 6px 8px;

            @include media("<mddesktop") {
                width: 150px;
            }
        }
        > a {
            position: absolute;
            right: 0;
            width: 30px;
            height: 30px;
            text-align: center;
            top: 50%;
            transform: translateY(-50%);
            color: #989898;
            display: flex;
            justify-content: center;
            align-items: center;
            &:hover {
                background-color: $aqua;
                color: $white;
            }
        }
    }
    .fa-search {
        font-size: 15px;
    }
}
.arrow {
    position: relative;
    width: 10px;
    margin-left: 15px;
    height: 8px;
    &::before,
    &::after {
        position: absolute;
        content: "";
        display: block;
        width: 5px;
        height: 1px;
        background-color: $gray4;
    }
    &::before {
        right: 4px;
        top: 50%;
        transform: translate(0, -50%) rotate(45deg);
    }
    &::after {
        right: 0px;
        top: 50%;
        transform: translate(0, -50%) rotate(-45deg);
    }
}

.open-menu {
    z-index: 40;
    @include media-breakpoint-up(lg) {
        display: none;
    }
    padding: 0 0 0 10px;
    height: 42px;
    .bar1,
    .bar2,
    .bar3 {
        height: 5px;
        z-index: 80;
        width: 30px;
        height: 2px;
        background-color: #333;
        margin: 7px 0;
        transition: 0.4s;
        display: block;
        &:first-of-type {
            margin-top: 14px;
        }
    }
    &.menu-opened {
        .bar2 {
            opacity: 0;
        }
        .bar1 {
            transform: rotate(-45deg) translate(0px, 8px);
        }
        .bar3 {
            transform: rotate(45deg) translate(-4px, -13px);
        }
    }
}

.banner {
    background-color: $black2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    color: $white;
    width: 100%;
    min-height: 200px;
    span {
        text-align: center;
        font-size: 36px;
        line-height: 48px;
        @include media-breakpoint-down(xs) {
            font-size: 28px;
            line-height: 40px;
        }
        &:first-child {
            font-weight: bold;
        }
    }
}
