.product {
    padding-top: 60px;
    padding-bottom: 120px;
    @include media-breakpoint-down(sm) {
        padding: 30px 0;
    }

    .row {
        margin-bottom: 40px;
    }
    .product-slider {
        position: relative;
        @include media-breakpoint-down(md) {
            margin-bottom: 10px;
        }
        .slick-arrow {
            z-index: 5;
            position: absolute;
            width: 30px;
            height: 40px;
            top: 50%;
            transform: translateY(-50%);
            cursor: pointer;
            display: flex !important;
            justify-content: center;
            align-items: center;
            transition: all 0.3s ease;
            color: $brown;
            &:hover {
                background-color: $aqua;
                color: $white;
            }
            i {
                font-size: 40px;
            }
        }
        .arrow-prev {
            left: 0;
        }
        .arrow-next {
            right: 0;
        }
    }
    .slide {
        position: relative;
        img {
            width: 100%;
        }
    }
    .product-description {
        > h5 {
            margin-bottom: 15px;
            @include media("<mddesktop") {
                margin-bottom: 8px;
            }
        }
        > p {
            margin-bottom: 20px;
            @include media("<mddesktop") {
                margin-bottom: 10px;
            }
        }
        > ul {
            font-size: 15px;
            line-height: 21px;
            font-family: $roboto;
            color: #8e8c8c;
            &:not(.description-row) {
                margin-bottom: 25px;
                list-style-type: disc;
                padding-left: 17px;
            }
        }
        > a {
            &:hover {
                i {
                    background-color: $aqua;
                }
            }
            i {
                margin-right: 5px;
                transition: all 0.3s ease;
                padding: 10px;
                border-radius: 50%;
                background-color: $gray;
                color: $white;
            }
        }
    }
    .description-row {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 8px;
        margin-left: -10px;
        margin-right: -10px;
        color: $gray;
        @include media("<mddesktop") {
            margin-bottom: 0;
        }
        > li {
            padding: 0 10px;
            margin-bottom: 8px;
            position: relative;
            &::after {
                content: "";
                position: absolute;
                right: 0;
                width: 2px;
                height: 60%;
                background-color: $gray;
                top: 2px;
            }
            &:first-child {
                padding-right: 0px;
                &::after {
                    display: none;
                }
            }
            &:last-child {
                display: flex;
                &::after {
                    display: none;
                }
            }
        }
    }
    .socials {
        display: flex;
        li {
            margin: 0 10px;
        }
    }
    .price {
        position: static;
        font-size: 30px;
        line-height: 100%;
        margin-bottom: 30px;
        @include media("<mddesktop") {
            margin-bottom: 10px;
        }
    }
    .product-details {
        margin-bottom: 20px;
        p {
            font-size: 13px;
            line-height: 30px;
            color: $brown;
            text-transform: uppercase;
            span {
                font-family: $roboto;
                font-size: 15px;
                line-height: 30px;
                text-transform: none;
                color: #8c8c8c;
            }
            &:last-child {
                span {
                    color: $aqua;
                }
            }
        }
    }

    .product-inputs {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;
        margin-left: -15px;
        margin-right: -15px;
        h5 {
            margin-bottom: 5px;
        }
        .input-group {
            flex: 0 1 33.333%;
            margin-bottom: 10px;
            padding: 0 15px;
            display: flex;
            flex-direction: column;
        }
    }

    .product-buttons {
        margin-bottom: 10px;
        .btn {
            margin-right: 20px;
            margin-bottom: 10px;
            &:hover {
                background-color: $black2;
            }
        }
    }
    .tabs-item {
        display: flex;
        flex-wrap: wrap;
        background-color: $white2;
        margin-bottom: 15px;
        @include media-breakpoint-down(xs) {
            flex-direction: column;
        }
        li {
            cursor: pointer;
            padding: 13px 15px;
            text-transform: uppercase;
            text-transform: uppercase;
            font-size: 16px;
            line-height: 100%;
            transition: all 0.3s ease;
            &:hover {
                background-color: $black2;
                color: $white;
            }
            &.active {
                background-color: $black2;
                color: $white;
            }
        }
    }
    .tab-content-item {
        h5 {
            margin-bottom: 10px;
        }
        p {
            font-size: 15px;
            line-height: 22px;
            margin-bottom: 20px;
        }
        &:not(:first-child) {
            display: none;
        }
    }
}
